<template>
  <div class="wrapper">
    <Header />
    <Notes :notes="notes" @delNote="delNote" @editNote="editNote" />
    <Modal v-show="isModalOpen" @close="isModalOpen = false" @addNote="addNote" :edit="edit" @changeNote="changeNote" />
    <button v-show="!isModalOpen" class="modal-btn" @click="(isModalOpen = true), (edit = false)">
      <img src="@/assets/img/edit.svg" alt="" />
    </button>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Notes from "@/components/Notes.vue";
import Modal from "@/components/Modal.vue";
export default {
  components: {
    Header,
    Notes,
    Modal,
  },
  data() {
    return {
      notes: [],
      isModalOpen: false,
      edit: false,
      noteId: null,
    };
  },
  methods: {
    changeNote(note) {
      let index = this.notes.findIndex((note) => note.id == this.noteId);
      this.notes[index].title = note.title;
      this.notes[index].text = note.text;
    },
    addNote(note) {
      this.notes.push(note);
    },
    delNote(id) {
      let index = this.notes.findIndex((note) => note.id == id);
      this.notes.splice(index, 1);
    },
    getNotes() {
      let localNotes = localStorage.notes;
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },
   
    editNote(id) {
      this.noteId = id;
      this.isModalOpen = this.edit = true;
    },
  },
  mounted() {
    this.getNotes();
  },
  watch: {
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
  },
};
</script>

