<template>
  <Transition name="bounce">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop="">
        <h2 class="modal__block-title">
          {{ edit ? "Изменить заметку" : "Добавить заметку" }}
        </h2>
        <div class="modal__block-inputs">
          <label for="inp">
            <span>Title</span>
            <input type="text" id="inp" v-model="title" />
          </label>
          <label for="area">
            <span>Content</span>
            <textarea id="area" v-model="text"></textarea>
          </label>
        </div>
        <div class="modal__block-btns">
          <button class="modal__block-btns-btn del" @click="closeModal">
            Отмена
          </button>
          <button v-if="!edit" class="modal__block-btns-btn edit" @click="addNote">
            Добавить
          </button>
          <button @click="newTask" v-else class="modal__block-btns-btn edit">
            Изменить
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
export default {
  props: {
    edit: {
      typeof: Boolean,
    },
  },
  data() {
    return {
      title: "",
      text: "",
    };
  },
  methods: {
    closeModal() {
      this.$emit("close");
      this.text = this.title = "";
    },
    addNote() {
      if (this.title != "" && this.text != "") {
        const note = {
          id: uuidv4(),
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
          complite:false
        };
        this.$emit("addNote", note);
        this.closeModal();
      }
    },
    newTask() {
      if (this.title != "" && this.text != "") {
        const note = {
          title: this.title,
          text: this.text,
          date: new Date().toLocaleDateString(),
        };
        this.$emit("changeNote", note);
        this.closeModal();
      }
    },
  },
};
</script>

<style>
.bounce-enter-active {
  animation: bounce-in 0.5s;
  transition: 0.2s;
}

.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
  transition: 0.2s;
}

.bounce-enter-from,
.bounce-leave-to {
  opacity: 0;
}

@keyframes bounce-in {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(1.25);
  }

  100% {
    transform: scale(1);
  }
}
</style>
