<template>
  <div class="notes__item" :class="{on:this.note.complite}">
    <div class="notes__item-top" :class="{ active: !view }">
      <h3 class="notes__item-top-title">{{ note.title }}</h3>
      <p class="notes__item-top-date">{{ note.date }}</p>
    </div>
    <p class="notes__item-text">{{ note.text }}</p>

    <div class="notes__item-btns">
      <button class="notes__item-btn edit" @click="$emit('editNote', note.id)">
        <img src="@/assets/img/edit.svg" alt="" />
        <span>редактировать</span>
      </button>
      <button class="notes__item-btn del" @click="$emit('delNote', note.id)">
        <img src="@/assets/img/del.svg" alt="" />
        <span>Удалить</span>
      </button>
      <button class="notes__item-btn complited" @click="mark">
        <span>Выполнено</span>
      </button>
    </div>
  </div>
</template>

<script>
    export default {
        props:{
            note:{
                typeof: Object
            },
            view:{
                typeof: Boolean
            }
        },
    methods: {
        mark(){
        if (this.note.id = this.note.id) {
            this.note.complite = true
        }else{
            console.log('error');
        }
        }
    },
    }
  
</script>

<style lang="scss" scoped>
</style>